body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.map
{
  width:100vw;
  height: 100vh;
  position: relative;
}

.location-icon
{
  font-size: 2rem;
  color: red;
}

.loader

{
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.header
{
  padding: 10px;
  background-color: firebrick;
  color: aliceblue;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}

.header h1,h3
{
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.loader img
{
  width: 400px;
}
.loader h1{
  margin-top: -50px;
}
.location-info
{
  position: absolute;
  top: 80px;
  right: 50px;
  width:400px;
  min-height: 50px;
  padding: 20px;
  background-color: rgba(0,0,0,0.6);
  border-radius: 10px;
  font-size: 18px;
  color:blanchedalmond;
}

.location-info ul{
  list-style-type: none;
  padding: 0;

}
.location-info li{
  padding: 5px 0;
}
